<script>
import MainLayout from '@/layouts/MainLayout';
import NoResult from '@/components/common/NoResult'
import Api from '@/helpers/api/index.js'
import filtersMixin from '@/mixins/filtersMixin.js'

// icon
import ArrowDownBold from 'mdi-vue/ArrowDownBold.vue'

export default {
    components: {
        MainLayout,
        NoResult,
        ArrowDownBold
    },
    mixins: [filtersMixin],
    data() {
        return {
            moment_from: '4 days',
            batches: {
                // total: 4,
                // data: [
                //     {
                //         status: 'PAID',
                //         orders: [{ orderid: 'S-9557-3309-9126', title: "Writer's choice", total: '$219.50' }, { orderid: 'S-9557-3309-9126', title: "Writer's choice", total: '$219.50' }],
                //         batchid: 'BA-9740-6182',
                //         created_at_local: { date: '10 Dec 2020' },
                //         paymethod: 'paypall',
                //         total: '$123'
                //     },
                //     {
                //         status: 'UNPAID',
                //         orders: [],
                //         batchid: 'BA-9740-6182',
                //         created_at_local: { date: '10 Dec 2020' },
                //         paymethod: 'paypall',
                //         total: '$123'
                //     }
                // ]
            },
            sort_order: 'DESC',
            sort_by: 'created_at',
            currentSorting: null,
            totalVisible: 10,
            page: 1,
            dataLoading: false,
            settingsBreadcrumbs: {
                title: 'Billing',
                steps: [
                    {
                        title: 'Home',
                        url: '/'
                    },
                    {
                        title: 'Account',
                        url: '/cp/account'
                    },
                    {
                        title: 'Billing',
                        url: '/cp/billing'
                    }
                ],
                excerpt:
                    "We call each payment a 'batch' because it can include more than one project. You can enter more than one project in the shopping cart during our ordering process.",
                additionalClass: 'bc-horizontal bc-billing'
            }
        }
    },
    mounted() {
        this.getData()
    },
    methods: {
        getData() {
            const vm = this
            // vm.dataLoading = true
            Api.post('/api/cp/batch/list', {
                page: this.page,
                sort_by: this.sort_by,
                sort_order: this.sort_order,
                per_page: 5,
                id: ''
            })
                .then((response) => {
                    console.log(response.data)
                    vm.batches = response.data;
                })
                .catch((error) => {
                    console.log(error)
                })
                .then(() => {
                    // vm.dataLoading = false
                })
        },
        changeLink(sort) {
            if (this.sort_by === sort) {
                this.sort_order = this.sort_order === 'ASC' ? 'DESC' : 'ASC'
            } else {
                this.sort_order = 'ASC'
            }
            this.sort_by = sort
            this.getData()
        }
    }
}
